.c-card {
  box-shadow: hsla(228, 87, 39, 15%) 0 40px 20px -20px;
  border-radius: 20px;
  width: 327px;
  height: 567px;

  background-color: hsl(0, 0%, 100%);
  .c-hero {
    margin-bottom: 32px;
    border-radius: 20px 20px 0 0;
    width: 327px;
    height: 160px;
  }
  .c-content-wrapper {
    flex-direction: column;
    align-items: center;

    display: flex;

    margin-inline: auto;
    width: 279px;
    .c-heading {
      margin-bottom: 16px;

      font-size: 22px;
      color: hsl(223, 47%, 23%);
    }
    .c-body {
      margin-bottom: 24px;
      width: 263px;

      font-size: 15px;
      font-weight: 700;
      line-height: 25px;

      text-align: center;
      color: hsl(224, 23%, 55%);
    }
    .c-price-wrapper {
      flex-direction: row;
      align-items: center;

      display: flex;

      margin-bottom: 24px;
      border-radius: 11px;
      width: 279px;
      height: 81px;

      background-color: hsl(225, 100%, 98%);
      .c-icon {
        margin-left: 16px;
      }
      .c-price-text-container {
        margin-left: 24px;
        width: 82px;
        .c-price-dark {
          margin-bottom: 4px;

          font-size: 14px;
          color: hsl(223, 47%, 23%);
        }
        .c-price-light {
          font-size: 14px;
          color: hsl(224, 23%, 55%);
        }
      }
      .c-price-edit {
        margin-left: 49px;

        font-size: 13px;
        text-decoration: underline;
        color: hsl(245, 75%, 52%);

        cursor: pointer;
      }
      .c-price-edit:hover {
        transition: 0.2s ease-in-out;

        color: hsl(245, 83, 68);
      }
    }
    .c-button {
      box-shadow: hsla(245, 75, 52, 19%) 0 20px 20px 0;
      margin-bottom: 24px;
      border-radius: 12px;
      border-style: none;
      width: 279px;
      height: 50px;

      font-size: 15px;
      color: hsl(0, 0%, 100%);

      background-color: hsl(245, 75%, 52%);

      cursor: pointer;
    }
    .c-button:hover {
      transition: 0.2s ease-in-out;

      background-color: hsl(245, 83, 68);
    }
    .c-button:active {
      transform: translateY(3px);
    }
    .c-cancel {
      font-size: 15px;
      color: hsl(224, 23%, 55%);

      cursor: pointer;
    }
    .c-cancel:hover {
      transition: 0.2s ease-in-out;

      opacity: 0.5;
    }
  }
}
@media only screen and (min-width: 1440px) {
  .c-card {
    width: 450px;
    height: 697px;
    .c-hero {
      margin-bottom: 45px;
      width: 450px;
      height: 220px;
    }
    .c-content-wrapper {
      width: 354px;
      .c-heading {
        margin-bottom: 17px;

        font-size: 28px;
      }
      .c-body {
        margin-bottom: 20px;
        width: 100%;

        font-size: 16px;
        line-height: 26px;
      }
      .c-price-wrapper {
        margin-bottom: 32px;
        width: 100%;
        height: 98px;
        .c-icon {
          margin-left: 24px;
        }
        .c-price-text-container {
          margin-left: 20px;
          width: 94px;
          .c-price-dark {
            font-size: 16px;
          }
          .c-price-light {
            font-size: 16px;
          }
        }
        .c-price-edit {
          margin-left: 92px;

          font-size: 14px;
        }
      }
      .c-button {
        margin-bottom: 32px;
        width: 100%;
      }
    }
  }
}
